import React from "react";

const Instructions = ({ instructions }) => {
	return (
		<div className="p-1 my-1">
			<h1 className="text-center py-1">Steps</h1>
			<ol>
				{instructions.map((instruction, i) => (
					<li key={i}>{instruction}</li>
				))}
			</ol>
		</div>
	);
};

export default Instructions;
