import React from "react";
import Header from "./components/Header";
import "./App.css";
import Video from "./components/Video";
import Img from "./components/Img";
import Instructions from "./components/Instructions";
import Ingredients from "./components/Ingredients";
import _ from "lodash";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      meal: {},
    };
  }

  // Grabbing list of ingredients
  ingredients = (meal) => {
    console.log(meal);
    return Object.entries(meal)
      .map((keys) => keys)
      .filter((key) => key[0].includes("Ingredient"))
      .filter((key) => key[1] !== "" && key[1] !== " ")
      .map((ingredient) => ingredient[1]);
  };

  measures = (meal) => {
    return Object.entries(meal)
      .map((keys) => keys)
      .filter((key) => key[0].includes("Measure"))
      .filter((key) => key[1] !== "" && key[1] !== " ")
      .map((measure) => measure[1]);
  };

  formatIngredients = (ingredients, measures) => {
    return measures.map((measure, i) => `${measure} ${ingredients[i]}`);
  };

  formatInstructions = (instructions) => {
    const arrInstructions = instructions.split(".");
    arrInstructions.pop();
    return arrInstructions;
  };

  onClick = () => {
    fetch("https://www.themealdb.com/api/json/v1/1/random.php")
      .then((res) => res.json())
      .then((res) => {
        this.setState({
          meal: {
            strMeal: res.meals[0].strMeal,
            strMealThumb: res.meals[0].strMealThumb,
            strInstructions: this.formatInstructions(
              res.meals[0].strInstructions
            ),
            strYouTube: res.meals[0].strYoutube
              .replace(
                res.meals[0].strYoutube.split("=")[0],
                "https://www.youtube.com/embed/"
              )
              .split("")
              .filter((a) => a !== "=")
              .join(""),
            strIngredients: this.formatIngredients(
              this.ingredients(res.meals[0]),
              this.measures(res.meals[0])
            ),
          },
        });
      })
      .catch((e) => {
        console.warn(e);
      });
  };
  render() {
    return _.isEmpty(this.state.meal) ? (
      <section>
        <Header res={this.state.meal} onClick={this.onClick} />
      </section>
    ) : (
      <section>
        <section className="header">
          <Header res={this.state.meal} onClick={this.onClick} />
        </section>
        <section className="container">
          <div className="title">
            <Img
              thumb={this.state.meal.strMealThumb}
              title={this.state.meal.strMeal}
            />
          </div>

          <div className="grid-2">
            <div className="card all-center">
              <Video youtube={this.state.meal.strYouTube} />
            </div>
            <div className="card ">
              <Ingredients ingredients={this.state.meal.strIngredients} />
            </div>
          </div>
          <div className="card">
            <Instructions instructions={this.state.meal.strInstructions} />
          </div>
        </section>
        <footer className=" all-center p-1">
          <p>By Julien</p>
        </footer>
      </section>
    );
  }
}
