import React from "react";

const Img = ({ thumb, title }) => {
	return (
		<div className="card all-center grid-2">
			<h1 className="py-2 text-center " style={{ fontSize: "1.5em" }}>
				{title}
			</h1>
			<img
				className="round-img"
				src={thumb}
				alt="meal"
				style={{ width: "300px" }}
			></img>
		</div>
	);
};

export default Img;
