import React from "react";

const Ingredients = ({ ingredients }) => {
	return (
		<div>
			<h1 className="text-center py-1 my-1">Ingredients</h1>
			<ul>
				{ingredients.map((ingredient, i) => (
					<li key={i}>{ingredient}</li>
				))}
			</ul>
		</div>
	);
};

export default Ingredients;
