import React from "react";
export default class Header extends React.Component {
	render() {
		return (
			<div>
				<div className="navbar bg-primary">
					<h1>Get your meal now !</h1>
					<button
						className="btn btn-light"
						onClick={this.props.onClick}
						name="randomize"
					>
						Get meal !
					</button>
				</div>
			</div>
		);
	}
}
